// DISPLAY HELPERS: GRID

.is-grid {
    &.is-spaced-small {
        grid-auto-rows: minmax(min-content, max-content);
        grid-gap: 1rem;
    }

    &.is-spaced-medium {
        grid-auto-rows: minmax(min-content, max-content);
        grid-gap: 3rem;
    }

    &.is-spaced-large {
        grid-auto-rows: minmax(min-content, max-content);
        grid-gap: 5rem;
    }
}

// DISPLAY HELPERS: FLEX

.is-flex,
.is-grid {
    &.is-align-center {
        align-items: center;
    }

    &.is-align-start {
        align-items: flex-start;
    }

    &.is-align-end {
        align-items: flex-end;
    }
}

.is-flex,
.is-grid {
    &.is-justify-center {
        justify-content: center;
    }
}


// DISPLAY HELPERS: ULTILITIES

.is-click-through {
    pointer-events: none;
}

.is-nonselectable {
    user-select: none;
    -webkit-user-select: none;
    -webkit-user-drag: none;
}


